<template>
<div id="landing_page">
      
      
      <div id="main_flip_container">

          <div id="flip_container">
           <div v-for="x in 40" :key="x" :style="{height: flipHeight+'px', width: flipWidth+'px'}" class="flip_card" @mouseover="flipHover">
              <div class="card__face card__face--front"></div>
              <div class="card__face card__face--back"></div>
            </div>
          </div> 

          <img id="landingPageImg" :src="landingPageImg">

      </div>

      
</div>
</template>

<script>
export default {
  data() {
    return {
      flipHeight: 0,
      flipWidth: 0,
      landingPageImg: require('@/assets/landing_img_placeholder.jpg'),
      
    }
  },
  methods: {
    flipHover(e){
      // only flip the .flip_card 
      var findClass = e.srcElement.offsetParent.className;
      if(findClass == 'flip_card'){

        e.srcElement.offsetParent.classList.add("is-flipped");
      }
    },
    appendFlipCard(){

    // get the max height and width
    var getHeight = document.getElementById('flip_container').clientHeight;
    var getWidth = document.getElementById('flip_container').clientWidth;
    // devide the max height and width
    this.flipHeight = getHeight / 8;
    this.flipWidth = getWidth / 5;


    }

  },
  mounted() {  
    this.appendFlipCard();
  },
  created() {
    // responsive function for .flip_card
    window.addEventListener("resize", this.appendFlipCard);
  },
  destroyed() {
    // responsive function for .flip_card
    window.removeEventListener("resize", this.appendFlipCard);
  },

}
</script>

<style>



#landingPageImg{
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 700px;
}

div#flip_container {
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 700px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    
}



.flip_card {
  cursor: pointer;
  background-color: transparent;

  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d; 
  -webkit-transition: -webkit-transform 0.5s; 
  transition: -webkit-transform 0.5s; 
  transition: transform 0.5s; 
  transition: transform 0.5s, -webkit-transform 0.5s;
  
}

.is-flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  
}

.card__face--front {
  background-color: #bbb;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
}

.card__face--back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}






</style>