<style>
path.ship, g.ship {
 	-webkit-animation-timing-function: linear;
 	        animation-timing-function: linear;
    -webkit-animation: sailing 15s infinite;
            animation: sailing 15s infinite;
    
}
@-webkit-keyframes sailing {

  0% {
   
    -webkit-transform: translateX(-400px) rotateY(0) scale(1);
   
            transform: translateX(-400px) rotateY(0) scale(1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
    
  }
  30% {
      
    -webkit-transform: translateX(400px) rotateY(0) scale(1.1);
      
            transform: translateX(400px) rotateY(0) scale(1.1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
  }
  50% {
      
    -webkit-transform: translateX(400px) rotateY(180deg) scale(1);
      
            transform: translateX(400px) rotateY(180deg) scale(1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
  }
  80% {
      
    -webkit-transform: translateX(-400px) rotateY(180deg) scale(1.1);
      
            transform: translateX(-400px) rotateY(180deg) scale(1.1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
  }
  100% {
     
    -webkit-transform: translateX(-400px) rotateY(360deg) scale(1);
     
            transform: translateX(-400px) rotateY(360deg) scale(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }

}
@keyframes sailing {

  0% {
   
    -webkit-transform: translateX(-400px) rotateY(0) scale(1);
   
            transform: translateX(-400px) rotateY(0) scale(1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
    
  }
  30% {
      
    -webkit-transform: translateX(400px) rotateY(0) scale(1.1);
      
            transform: translateX(400px) rotateY(0) scale(1.1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
  }
  50% {
      
    -webkit-transform: translateX(400px) rotateY(180deg) scale(1);
      
            transform: translateX(400px) rotateY(180deg) scale(1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
  }
  80% {
      
    -webkit-transform: translateX(-400px) rotateY(180deg) scale(1.1);
      
            transform: translateX(-400px) rotateY(180deg) scale(1.1);
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
  }
  100% {
     
    -webkit-transform: translateX(-400px) rotateY(360deg) scale(1);
     
            transform: translateX(-400px) rotateY(360deg) scale(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }

}





circle.falling_star{
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation: star 5s infinite;
            animation: star 5s infinite;
}
@-webkit-keyframes star {

  0% {
    cx: 1298;
    cy: 111;
  }

  100% {
	cx: 1043.8;
    cy: 303.8;
    opacity: 0.1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=10)";
  }

}
@keyframes star {

  0% {
    cx: 1298;
    cy: 111;
  }

  100% {
	cx: 1043.8;
    cy: 303.8;
    opacity: 0.1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=10)";
  }

}

path.falling_star {
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation: star_light 5s infinite;
            animation: star_light 5s infinite;

}
@-webkit-keyframes star_light {

0% {
    -webkit-transform: translate(257px, -195px);
            transform: translate(257px, -195px);
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  }

100% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }

}
@keyframes star_light {

0% {
    -webkit-transform: translate(257px, -195px);
            transform: translate(257px, -195px);
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  }

100% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }

}






path.mist {
