<template>
<div id="boxBorderLight">


    <div class="box_border box_red">
    </div>

    <div class="box_border box_purple">
    </div>

    <div class="box_border box_blue">
    </div>    

    
</div>  
  
</template>

<script>
export default {
    name: "Box Border Light"
}
</script>

<style>
#boxBorderLight{
    background-color: #181818;
    display: flex;
    justify-content: space-evenly;
    height: 100vh;
    align-items: center;
}


.box_border{
    width: 200px;
    height: 200px;
    box-shadow: 16px 14px 20px #0000008c;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* BOX - RED */
.box_red::before{
    content: "";
    background-image: conic-gradient(#ff0052 20deg, transparent 120deg);
    width: 150%;
    height: 150%;
    position: absolute;

    /* Animation */
    animation: rotate 2s linear infinite;
}

.box_red::after{
    content: "Animation";
    width: 190px;
    height: 190px;
    background-color: #101010;
    border-radius: 10px;
    position: absolute;
    /* center the text */
    display: flex;
    align-items: center;
    justify-content: center;
    /* text style */
    color: #ff0052;
    font-size: larger;
    letter-spacing: 5px;

    /* invers shadow */
    box-shadow: inset 20px 20px 20px #0000008c;

}


/* BOX - PURPLE */
.box_purple::before{
    content: "";
    background-image: conic-gradient(#C002D9 20deg, transparent 120deg);
    width: 150%;
    height: 150%;
    position: absolute;

    /* Animation */
    animation: rotate 2s linear infinite;
}

.box_purple::after{
    content: "Animation";
    width: 190px;
    height: 190px;
    background-color: #101010;
    border-radius: 10px;
    position: absolute;
    /* center the text */
    display: flex;
    align-items: center;
    justify-content: center;
    /* text style */
    color: #C002D9;
    font-size: larger;
    letter-spacing: 5px;

    /* invers shadow */
    box-shadow: inset 20px 20px 20px #0000008c;

}

/* BOX - BLUE */
.box_blue::before{
    content: "";
    background-image: conic-gradient(#660AF0 20deg, transparent 120deg);
    width: 150%;
    height: 150%;
    position: absolute;

    /* Animation */
    animation: rotate 2s linear infinite;
}

.box_blue::after{
    content: "Animation";
    width: 190px;
    height: 190px;
    background-color: #101010;
    border-radius: 10px;
    position: absolute;
    /* center the text */
    display: flex;
    align-items: center;
    justify-content: center;
    /* text style */
    color: #660AF0;
    font-size: larger;
    letter-spacing: 5px;

    /* invers shadow */
    box-shadow: inset 20px 20px 20px #0000008c;    

    /* image in box */   
    background-image: url("~@/assets/logo.png");
    background-position: center center;
    background-repeat: no-repeat;

}


@keyframes rotate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(-360deg);
    }
}




</style>